.container {
  padding-top: 50px;
  max-height: 100%;
  max-width: 100%;
  transition: all 1s ease;
  opacity: 0;
  visibility: hidden;


  &.visible {
    opacity: 1;
    visibility: visible;
  }

  .divideLineBlock {
    margin: 40px auto;
    width: 66.6666666667%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .line {
      height: 1px;
      background-color: #ddd;
      width: 40%;
      margin: 0 5px;
    }

    .letterIcon {
      color: #ffb400;

      svg {
        width: 20px;
        height: 20px;
      }
    }

  }

  .contentRow {
    padding: 0 24px;
    display: flex;
    max-height: max-content;

    .leftSide {
      padding: 18px;
      border: 1px solid #ddd;
      background: #f2f2f2;
      min-width: 250px;

      >h6 {
        margin: 18px 0;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;
        color: #ffb400;

        &:first-child {
          margin-top: 0;
        }
      }

      > span {
        svg {
          color: rgba(0, 0, 0, .2);
          font-size: 16px;
          margin-right: 15px;
        }


      }

      .socialNetworks {
        display: flex;
        justify-content: space-around;
        //align-items: center;
        li {
          //margin-right: 20px;

          a {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            height: 35px;
            width: 35px;
            border-radius: 50%;

            svg {
              margin: 0 auto;
              color: #fff;

            }
          }

          .facebook {
            background-color: #3b5998;
            font-size: 16px;
          }

          .linkedin {
            background-color: #0077b5;
            font-size: 16px;
          }

          .github {
            box-sizing: border-box;

            img {
              height: 35px;
              width: 35px;
            }
          }
        }

      }
    }

    .rightSide {
      padding: 0 1.5rem;
      position: relative;

      h6 {
        font-weight: 700;
        text-transform: uppercase;
        font-size: 1.15rem;
        line-height: 110%;
        color: #656c6d;
      }

      .text {
        font-size: 14px;
        margin-top: 14px;
        //width: 83.3333333333%;
        box-sizing: border-box;
        //padding: 0 .75rem;
        min-height: 1px;
        line-height: 2;
        color: #656c6d;
        font-weight: 400;
      }

    }
  }

  .closeButton {
    position: absolute;
    top: 32px;
    right: 48px;
    width: 30px;
    cursor: pointer;
    z-index: 100;
    color: #656c6d;

    svg {
      width: 100%;
      height: 100%;
      transition: color 0.3s ease;

      &:hover {
        color: #ffb400;
      }
    }
  }
}
@media (max-width: 600px) {
  .container {
    .closeButton {
      width: 20px;
      top: 20px;
      right: 20px;
    }
  }
}