.container {
  transition: all 1s ease;
  opacity: 0;
  visibility: hidden;

  &.visible {
    opacity: 1;
    visibility: visible;
  }

  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  h2 {
    font-size: 55px;
    color: #666;
    text-align: center;
    //font-weight: 900;
    text-transform: uppercase;
    line-height: 110%;
    margin: 2.3733333333rem 0 1.424rem 0;

    span:nth-child(2) {
      color: #ffb400;
    }
  }

  .divideLineBlock {
    margin: 40px auto;
    width: 66.6666666667%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .line {
      height: 1px;
      background-color: #ddd;
      width: 40%;
      margin: 0 5px;
    }

    .letterIcon {
      color: #ffb400;

      svg {
        width: 20px;
        height: 20px;
      }
    }

  }

  .imgPlusPersonalInfosWrap {
    .mainPictureInner {
      display: none;
    }

    .personalInfosBlock {
      padding: 0 24px 50px;
      border-bottom: 1px solid #efe9e9;

      &:after {
        content: " ";
        display: table;
        clear: both;
      }

      > div:nth-child(1) {
        display: flex;
        align-items: center;
        color: #656c6d;
        margin-bottom: 10px;

        svg {
          margin-right: 16px;
        }

        h6 {
          margin: 0;
          padding: 0;
          text-transform: uppercase;
          font-size: 22px;
          line-height: 110%;
        }
      }

      .myCommonInfo {
        p {
          margin: 0 auto 10px;
          font-size: 20px;
          line-height: 2;
          color: #656c6d;
          font-weight: 400;
        }
      }

      .exactlyMyInfo {
        width: 50%;
        margin-left: auto;
        left: auto;
        right: auto;
        float: left;
        color: #656c6d;

        ul {
          li {
            padding-bottom: 13px;
            font-size: 16px;

            span {
              color: #222;
              font-weight: 700;
            }
          }
        }
      }

      a {
        font-weight: 700;
        height: 42px;
        line-height: 42px;
        font-size: 15px;
        float: left;
        //display: flex;
        text-decoration: none;
        color: #fff;
        text-align: center;
        letter-spacing: .5px;
        cursor: pointer;
        text-transform: uppercase;
        vertical-align: middle;
        padding: 0 2rem;

        &.btn:not(.btnBlog) {
          margin-right: 24px;
          background: #ffb400;
        }

        &.btn:hover {
          box-shadow: none;
          color: #eee;
        }

        &.btnBlog {
          border: 1px solid #ffb400;
          color: #ffb400;
          background: transparent;
          transition: .1s;

          svg {
            color: #ffb400;
          }
        }

        &.btnBlog:hover {
          color: #fff;
          background: #ffb400;

          svg {
            color: #fff;
          }
        }

        svg {
          font-size: 22px;
          padding-left: 8px;
          //line-height: inherit;
          color: #fff;
        }
      }

    }
  }

  .sliderWrap {

    display: flex;
    //height: max-content;
    height: 800px;
    //align-items: center;

    .controlsBlock {
      padding: 0 24px;
      width: 33.3333333333%;
      left: auto;
      right: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .controlBtn {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        background: rgba(0, 0, 0, 0.1) !important;
        box-shadow: none;
        padding: 24px;
        margin: 24px 0;
        cursor: pointer;
        user-select: none;
        color: #656c6d;
        line-height: 1.5;
        font-size: 22px;
        background: #fff !important;
        width: 100%;
        border: 1px solid #ddd;

        svg {
          margin-right: 10px;
        }

        h6 {
          display: inline-block;
          text-transform: uppercase;
          //font-size: 1.15rem;
          line-height: 110%;
          font-size: 22px;
          font-weight: 600;
        }

        &.active {
          background: #ffb400 !important;
          color: #fff;
        }
      }

    }

    .slidesBlockWrap {
      margin-left: auto;
      left: auto;
      right: auto;
      padding: 90px 24px 0;
      width: 66.6666666667%;


      .slidesBlock {
        position: relative;
        height: 100%;
        width: 100%;

        .slide {
          position: absolute;
          height: max-content;

          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          color: #656c6d;
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
          transition: all 0.3s ease;
          background-color: white;

          &:nth-child(3) {
            margin-bottom: 24px;
          }

          &.active {
            z-index: 25;
          }

          &.neutral {
            z-index: 15;
            transform: scale(0.9) translateY(-75px);

            &:hover {
              transform: scale(0.9) translateY(-90px);
              cursor: pointer;
            }
          }

          &.passive {
            transform: scale(0.8) translateY(-150px);
            z-index: 5;

            &:hover {
              transform: scale(0.8) translateY(-165px);
              cursor: pointer;
            }
          }

          .resumeCardHeader {
            text-transform: uppercase;
            font-weight: bold;
            font-size: 21px;
            padding: 24px;
            text-align: center;
            background: #f2f2f2;
            border-bottom: 1px solid #ddd;
            color: #ffb400;
            margin-bottom: 5px;
            display: flex;
            align-items: center;
            justify-content: center;


            h6 {
              margin-left: 4px;
              display: inline-block;
              //text-transform: uppercase;
              font-weight: bold;
              font-size: 28px;
            }
          }

          .resumeCardBody {

            .experience {
              padding: 24px;
              display: flex;
              flex-flow: column;

              .resumeContent {
                position: relative;
                color: #656c6d;

                .icon {
                  color: blue;
                }

                h6 {
                  margin-bottom: 10px;
                  font-size: 15px;
                  margin-top: 0;
                  text-transform: uppercase;
                  line-height: 110%;
                  color: #656c6d;

                  > span {
                    //margin-left: 10px;
                    //font-weight: 600;
                  }

                }

                > span {
                  font-weight: 400;
                  color: #aaa;
                  font-size: 13px;

                  svg {
                    //padding-right: 10px;
                    //color: white;
                  }

                  > span {
                    margin-left: 10px;
                  }

                }

                > p {
                  font-size: 14px;
                  padding-bottom: 20px;
                  margin-top: 5px;
                  line-height: 2;
                }
              }

              .separator {
                height: 2px;
                width: 70px;
                margin-bottom: 24px;
                background: #ffb400;
              }
            }

            .counterContainer {
              padding: 24px;
              display: flex;
              flex-flow: column;

              .row {
                margin-left: -.75rem;
                margin-right: -.75rem;
                //margin-bottom: 20px;

                &:after {
                  content: " ";
                  display: table;
                  clear: both;
                }

                .col {
                  float: left;
                  padding: 0 .75rem;
                  min-height: 1px;
                  width: 50% !important;

                  .resumeContent {
                    position: relative;

                    > h6 {
                      font-size: 15px;
                      margin-top: 0;
                      text-transform: uppercase;
                      line-height: 110%;
                      margin: .7666666667rem 0 .46rem 0;
                      font-weight: 400;
                    }

                    > p {
                      font-size: 14px;
                      padding-bottom: 20px;
                      margin: 0 auto;
                      margin-top: 5px;
                      color: #ffb400;
                      line-height: 2;
                      //color: #656c6d;
                      font-weight: 400;

                    }
                  }
                }
              }
            }
          }
        }
      }
    }

  }

  .closeButton {
    position: absolute;
    top: 32px;
    right: 48px;
    width: 30px;
    cursor: pointer;
    z-index: 100;
    color: #656c6d;

    svg {
      width: 100%;
      height: 100%;
      transition: color 0.3s ease;

      &:hover {
        color: #ffb400;
      }
    }
  }
}

@media (max-width: 1000px) {
  .container {
    .imgPlusPersonalInfosWrap {
      display: flex;
      border-bottom: 1px solid #efe9e9;

      .mainPictureInner {
        display: block;
        //width: 33.3333333333%;
        img {
          width: 100%;
          height: max-content;
          object-fit: cover;
          object-position: 65% 20%;
        }
      }

      .personalInfosBlock {
        border-bottom: none;

        .exactlyMyInfo {
          float: none;
          width: 100%;
        }
      }
    }

    .sliderWrap {
      .controlsBlock {
        width: 100%;
      }

      .slidesBlockWrap {
        width: 100%;
      }
    }
  }
}

@media (max-width: 800px) {
  .container {
    .imgPlusPersonalInfosWrap {
      a {
        float: none;
        display: block;
        width: 100%;
        &.btn:not(.btnBlog) {
          margin-bottom: 24px;

        }
      }
    }
    .sliderWrap {
      height: max-content;

      .controlsBlock {
        display: none;
      }

      .slidesBlockWrap {
        .slidesBlock {
          position: static;
          .slide {
            position: static;
            margin-bottom: 24px;

            &.active {
              z-index: 25;
            }

            &.neutral {
              z-index: 15;
              transform: scale(1) translateY(0);

              &:hover {
                transform: scale(1) translateY(0);
                cursor: auto;
              }
            }

            &.passive {
              transform: scale(1) translateY(0);
              z-index: 5;

              &:hover {
                transform: scale(1) translateY(0);
                cursor: auto;
              }
            }

            .resumeCardBody {
              .experience {
                .resumeContent {
                  position: static;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .container {
    .imgPlusPersonalInfosWrap {
      display: block;
      .mainPictureInner {
        padding: 0 24px;
        margin-bottom: 15px;
        img {
        }
      }

      .personalInfosBlock {
        .exactlyMyInfo {

        }
      }
    }

    .sliderWrap {
      .controlsBlock {
      }

      .slidesBlockWrap {
        .slidesBlock {
          .slide {

            .resumeCardBody {
              .experience {
                .resumeContent {
                }
              }
              .counterContainer{
                .row {
                  .col {
                    padding: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
    .closeButton {
      width: 20px;
      top: 20px;
      right: 20px;
    }
  }
}


